body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100%;
  height: 100%;
}

.left{
  width:150px;
  min-height: 100vh;
  background:#292a2a;
  float: left;
  text-align: center;
  color: #525353;
}
.logo{
  width: 100px;
  height:100px;
  margin-left: 25px;
  margin-top: 50px;
}
.logo img{
  width: 100%;
  height: 100%;
}
.list{
  margin-top: 50px;
}
.select{
  width: 150px;
  height: 80px;
  line-height: 80px;
}
.show{
  background: #525353;
  color: #fff;
}
.select:hover{
  background: #d4237a;
  color: #fff;
  cursor: pointer;
}
.markdown{
  overflow: hidden;
}
.center{
  width: 50%;
  height: 100vh;
  float: left;
  box-sizing: border-box;
}
.center .quill .ql-toolbar{
 width: 100%;
 position: fixed;
 z-index: 1000;
 background: #fff;
}
.center .quill .ql-container{
  height: 100vh;
  padding-top: 40px;
}
.editor{
  overflow: hidden;
}
.editor::after{
  content: '';
  clear: both;
  display: block;
  height: 0;
  overflow: hidden;
}
.center .w-e-toolbar{
  position: fixed;
  width: 100%;
}
.right{
  float: left;
  width: 50%;
  height: 100vh;
  padding-top: 33px;
  padding-left: 15px;
  box-sizing: border-box;
  overflow-y: auto;
}
.right pre{
   background: #d4ebf8;
   padding-bottom: 15px;
}
.right p code{
  color: #d4237a;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

